import React, { ReactNode } from "react"
import { graphql, useStaticQuery } from "gatsby"
import { Container } from "../container"
import { StyledFooter } from "./style"

interface ISocial {
	icon: ReactNode
	to: string
}

interface IFooterProps {
	logo: ReactNode
	social: ISocial[]
}

export const Footer = ({ logo, social }: IFooterProps) => {
	
	const data = useStaticQuery(graphql`
	query Images{
	  image: file(relativePath: { eq: "pingmyfeeling.png" }) {
		childImageSharp {
		  fluid {
			...GatsbyImageSharpFluid
		  }
		}
	  }
	}
  `)

	return(
	<StyledFooter>
		<Container>
			<div className="footer__wrapper">
				<a
					className="footer__logo"
					href="https://astuary.github.io/Kunjal"
					target="_blank"
					rel="noopener noreferrer"
				>
				<img src={data.image.childImageSharp.fluid.src}/>
				</a>
				<ul>
					{social.map((item) => (
						<li key={item.to}>
							<a
								href={item.to}
								target="_blank"
								rel="noopener noreferrer"
							>
								{item.icon}
							</a>
						</li>
					))}
				</ul>
			</div>
		</Container>
	</StyledFooter>
)
}